.product_container img {
  width: 100%;
}

.image_container {
  position: relative;
}

.label {
  position: absolute;
  z-index: 2;
  left: 10px;
  top: 0px;
  color: white;
  background-color: red;
  font-family: "LFutura";
  font-size: 20px;
}

.label p {
  margin: 5px;
}

.quickView {
  position: absolute;
  width: 100%;
  left: 0;
  top: 81%;
  z-index: 2;
  background-color: #fff;
  opacity: 80%;
  font-family: "LFutura";
  font-size: 15px;
  color: black;
}

.download {
  position: absolute;
  background-color: #fff;
  opacity: 80%;
  left: 88%;
  top: 8%;

  width: 35px;
  height: 35px;
  border-radius: 50%;
}

.download svg {
  padding-top: 8px;
}

.product_name,
.product_price {
  font-family: "LFutura";
  font-weight: lighter;
}

.product_name {
  margin: 2px 0;
}

.product_price {
  margin-top: 2px;
}

.product_info {
  text-align: start;
}

.container {
  margin: 0px 10px;
  cursor: pointer;
}

.add_cart {
  background-color: #585858;
}

.add_cart p {
  color: white;
  font-family: "LFutura";
  padding: 5px;
}

.isHoveredShow {
  visibility: hidden;
}

@media screen and (max-width: 1160px) {
  .quickView {
    top: 75%;
  }
}

@media screen and (max-width: 860px) {
  .isHoveredShow {
    visibility: visible;
  }
  .quickView {
    top: 77%;
  }
}
