.about_container {
  background-image: url("../../assets/10020.png");
  background-size: cover;
  background-repeat: no-repeat;
  min-height: 200vh;
  background-position: center;
  margin-top: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-attachment: fixed;
}

.about_content_container {
  text-align: center;
  background-color: white;
  padding: 72px 0;
}

.about_content {
  margin: 24px 84px 84px;
  text-align: justify;
  font-family: "LFutura";
  font-size: 24px;
}
