ul {
  list-style-type: none;
  align-items: center;
  width: 100%;
  padding-inline-start: 0;
}

.contact_container {
  text-align: center;
}

.contact_form input,
.contact_form textarea {
  margin: 5px;
  padding-left: 10px;
  font-family: "LFutura";
  font-size: 16px;
  width: 60%;
}

.contact_form input {
  height: 36px;
}

.contact_form textarea {
  resize: none;
  width: 60%;
  height: 72px;
}

.email_container {
  width: 60%;
  margin: auto;
  display: flex;
  justify-content: center;
}

.contact_form input:hover,
.contact_form textarea:hover {
  border: 2px solid black;
}

.submit_btn_container {
  width: 60%;
}

.form_container li:last-child {
  display: flex;
  justify-content: center;
}

.submit_btn_container {
  display: flex;
  justify-content: flex-end;
  margin-right: 0;
}

.submit_btn {
  background-color: rgb(40, 40, 40);
  color: white;
  font-family: "LFutura";
  padding: 10px 36px;
  font-size: 18px;
  cursor: pointer;
}

.join_container {
  text-align: center;
}

.subscribe_btn {
  background-color: rgb(40, 40, 40);
  color: white;
  font-family: "LFutura";
  padding: 10px 36px;
  font-size: 18px;
  margin-left: 20px;
}

.email_container input {
  font-family: "LFutura";
  font-size: 16px;
  padding-left: 10px;
  flex: 4;
}

.email_container input:hover {
  border: 2px solid black;
}

.email_container button {
  flex: 1;
  cursor: pointer;
}

@media screen and (max-width: 1108px) {
  .subscribe_btn {
    font-size: 15px;
  }
}

@media screen and (max-width: 960px) {
  .subscribe_btn {
    padding: 10px 18px;
  }
}

@media screen and (max-width: 860px) {
  .subscribe_btn {
    font-size: 9px;
  }
}
