.navbar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  background-color: white;
  border-radius: 100px;
}

.navbar p a {
  text-decoration: none;
  color: black;
  font-family: var(--font_title);
  font-size: 40px;
  padding: 20px;
}

.navbar p a:hover {
  color: brown;
}

.toggleMenu {
  display: none;
}

@media screen and (max-width: 1160px) {
  .navbar p a {
    font-size: 20px;
  }

  .content_menu {
    text-align: center;
  }
}

@media screen and (max-width: 860px) {
  .toggleMenu {
    display: flex;
    margin-top: 20px;
  }

  .closeIcon {
    background-color: white;
  }

  .menu {
    display: none;
  }

  .openMenu {
    display: flex;
    flex-direction: row-reverse;
    padding-top: 50px;
  }

  .toggleButton {
    margin-right: 20px;
  }

  .navbar {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    background-color: white;
    border-radius: 0;
  }
}
