.footer_container {
  display: flex;
  width: 60%;
  margin: auto;
  justify-content: space-between;
  border-top-style: solid;
  border-width: thin;
  margin-top: 5rem;
  align-items: center;
}

/* .link_container {
  display: flex;
  flex-direction: row;
  align-items: center;
} */

.link {
  display: flex;
  flex-direction: row;
  margin-top: 1rem;
  height: 3rem;
  flex: 3;
  align-items: center;
  font-family: "LFutura";
}

.icon {
  flex: 1;
  margin-top: 1rem;
  text-align: right;
}

.icon svg {
  height: 1.5rem;
  width: 1.5rem;
  padding: 0.5rem;
  cursor: pointer;
}

.faq p,
.download_refunds p,
.policy p {
  font-size: 15px;
  padding: 10px;
  cursor: pointer;
}

.faq p:hover,
.download_refunds p:hover,
.policy p:hover {
  text-decoration: underline;
  text-decoration-thickness: 0.5px;
}

.faq p,
.download_refunds p {
  border-right-style: solid;
  border-width: thin;
}

.copyright {
  font-family: "LFutura";
  text-align: center;
  margin-top: 5rem;
}

@media screen and (max-width: 833px) {
  .icon svg {
    height: 1rem;
    width: 1rem;
  }
}

@media screen and (max-width: 670px) {
  .icon svg {
    height: 1em;
    width: 1rem;
    padding: 2px;
  }

  .faq p,
  .download_refunds p,
  .policy p {
    font-size: 10px;
    padding: 5px;
  }
}
