.heading {
  height: 700px;
  width: auto;
  background-image: url("../../assets/bg.jpg");
  background-size: auto;
  background-repeat: no-repeat;
  background-position: center;
}

.content {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
}

.content img {
  width: 100px;
  height: 100px;
  margin: 30px auto;
}

.break_line {
  border-top-style: solid;
  border-width: thin;
}

.break_line p {
  font-family: "LFutura";
  font-size: 20px;
}

@media screen and (max-width: 1160px) {
  .heading {
    background-size: auto;
  }
  .content {
    display: flex;
    flex-direction: row;
    text-align: center;
    align-items: center;
    justify-content: space-around;
  }

  .content img {
    margin: 20px;
  }

  .break_line {
    border-left-style: solid;
    border-width: thin;
    border-top: none;
  }

  .break_line p {
    padding-left: 10px;
  }

  .content_text {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
}

@media screen and (max-width: 860px) {
  .content {
    justify-content: space-between;
  }
}
