.title {
  font-family: "book antiqua", "palatino linotype", serif;
  font-size: 72px;
  margin-bottom: 0;
}

.subtitle {
  font-family: "LFutura";
  font-size: 36px;
  margin-top: 20px;
}

.artwork_content {
  text-align: center;
  margin-top: 0;
}

.product_list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  margin: auto 20px;
  text-align: center;
}

@media screen and (max-width: 860px) {
  .product_list {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (max-width: 430px) {
  .product_list {
    grid-template-columns: repeat(1, 1fr);
  }
}
